'use client'

import { useState, useEffect } from 'react'
import { useSession } from 'next-auth/react'
import { ChevronDown, ChevronUp, BarChart3, TrendingUp, Clock, CheckCircle } from 'lucide-react'
import { BarChart, Bar, LineChart, Line, PieChart, Pie, Cell, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts'

type BacklogItem = {
  id: number
  name: string
  job_type: string
  routeType: string
  cust_pn: string | null
  rev: string | null
  build_spec_pdigm: string | null
  proc_id: number
  group_id: number
  status_id: number
  startTime: string
  endTime: string | null
}

export default function DashboardPage() {
  const { data: session } = useSession()
  const [teamBacklog, setTeamBacklog] = useState<BacklogItem[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [teamBacklogOpen, setTeamBacklogOpen] = useState(true)
  const [myBacklogOpen, setMyBacklogOpen] = useState(true)

  useEffect(() => {
    fetchTeamBacklog()
  }, [])

  const fetchTeamBacklog = async () => {
    try {
      setLoading(true)
      setError(null)

      const res = await fetch('/api/dashboard/team-backlog')
      if (!res.ok) {
        throw new Error('Failed to fetch team backlog')
      }

      const data = await res.json()
      setTeamBacklog(data.results || [])
    } catch (err) {
      console.error('Error fetching team backlog:', err)
      setError(err instanceof Error ? err.message : 'Failed to load data')
    } finally {
      setLoading(false)
    }
  }

  // Calculate statistics
  const stats = {
    total: teamBacklog.length,
    byType: teamBacklog.reduce((acc, item) => {
      acc[item.job_type] = (acc[item.job_type] || 0) + 1
      return acc
    }, {} as Record<string, number>),
    byYear: teamBacklog.reduce((acc, item) => {
      const year = new Date(item.startTime).getFullYear()
      acc[year] = (acc[year] || 0) + 1
      return acc
    }, {} as Record<number, number>),
    byRoute: teamBacklog.reduce((acc, item) => {
      acc[item.routeType] = (acc[item.routeType] || 0) + 1
      return acc
    }, {} as Record<string, number>)
  }

  // Prepare chart data
  const typeChartData = Object.entries(stats.byType).map(([name, value]) => ({
    name,
    count: value
  }))

  const yearChartData = Object.entries(stats.byYear)
    .sort(([a], [b]) => Number(a) - Number(b))
    .map(([year, count]) => ({
      year,
      count
    }))

  const routeChartData = Object.entries(stats.byRoute).map(([name, value]) => ({
    name,
    count: value
  }))

  const COLORS = ['#3b82f6', '#10b981', '#f59e0b', '#ef4444', '#8b5cf6', '#ec4899', '#14b8a6']

  if (loading) {
    return (
      <div className="p-6">
        <div className="animate-pulse space-y-4">
          <div className="h-8 bg-slate-200 rounded w-1/4"></div>
          <div className="h-64 bg-slate-200 rounded"></div>
        </div>
      </div>
    )
  }

  return (
    <div className="p-6 space-y-4">
      {/* Header */}
      <div className="mb-6">
        <h1 className="text-3xl font-bold text-slate-800">Dashboard</h1>
        <p className="text-slate-600 mt-1">
          Welcome back, {session?.user?.name || session?.user?.email}
        </p>
      </div>

      {/* Quick Stats */}
      <div className="grid grid-cols-4 gap-4 mb-6">
        <div className="bg-white rounded-lg shadow p-6 border-l-4 border-blue-600">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm text-slate-600 font-medium">Total Jobs</p>
              <p className="text-3xl font-bold text-slate-800 mt-1">{stats.total}</p>
            </div>
            <BarChart3 className="text-blue-600" size={40} />
          </div>
        </div>

        <div className="bg-white rounded-lg shadow p-6 border-l-4 border-green-600">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm text-slate-600 font-medium">Job Types</p>
              <p className="text-3xl font-bold text-slate-800 mt-1">{Object.keys(stats.byType).length}</p>
            </div>
            <TrendingUp className="text-green-600" size={40} />
          </div>
        </div>

        <div className="bg-white rounded-lg shadow p-6 border-l-4 border-amber-600">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm text-slate-600 font-medium">Routes</p>
              <p className="text-3xl font-bold text-slate-800 mt-1">{Object.keys(stats.byRoute).length}</p>
            </div>
            <Clock className="text-amber-600" size={40} />
          </div>
        </div>

        <div className="bg-white rounded-lg shadow p-6 border-l-4 border-purple-600">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm text-slate-600 font-medium">This Year</p>
              <p className="text-3xl font-bold text-slate-800 mt-1">
                {stats.byYear[new Date().getFullYear()] || 0}
              </p>
            </div>
            <CheckCircle className="text-purple-600" size={40} />
          </div>
        </div>
      </div>

      {/* Team Backlog Accordion */}
      <div className="bg-white rounded-lg shadow">
        <button
          onClick={() => setTeamBacklogOpen(!teamBacklogOpen)}
          className="w-full flex items-center justify-between p-6 hover:bg-slate-50 transition-colors"
        >
          <div className="flex items-center gap-3">
            <BarChart3 className="text-blue-600" size={24} />
            <div className="text-left">
              <h2 className="text-xl font-bold text-slate-800">Team Backlog</h2>
              <p className="text-sm text-slate-600">Departmental throughput and analytics</p>
            </div>
          </div>
          {teamBacklogOpen ? <ChevronUp size={24} /> : <ChevronDown size={24} />}
        </button>

        {teamBacklogOpen && (
          <div className="p-6 border-t border-slate-200">
            {error ? (
              <div className="bg-red-50 border border-red-200 text-red-700 p-4 rounded-lg">
                <p className="font-semibold">Error loading data</p>
                <p className="text-sm">{error}</p>
                <button 
                  onClick={fetchTeamBacklog}
                  className="mt-2 px-4 py-2 bg-red-600 text-white rounded hover:bg-red-700"
                >
                  Retry
                </button>
              </div>
            ) : (
              <div className="space-y-6">
                {/* Charts Grid */}
                <div className="grid grid-cols-2 gap-6">
                  {/* Jobs by Type */}
                  <div className="bg-slate-50 rounded-lg p-4">
                    <h3 className="text-lg font-semibold text-slate-800 mb-4">Jobs by Type</h3>
                    <ResponsiveContainer width="100%" height={300}>
                      <PieChart>
                        <Pie
                          data={typeChartData}
                          cx="50%"
                          cy="50%"
                          labelLine={false}
                          label={({ name, percent }) => `${name}: ${(percent * 100).toFixed(0)}%`}
                          outerRadius={80}
                          fill="#8884d8"
                          dataKey="count"
                        >
                          {typeChartData.map((entry, index) => (
                            <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                          ))}
                        </Pie>
                        <Tooltip />
                      </PieChart>
                    </ResponsiveContainer>
                  </div>

                  {/* Jobs by Year */}
                  <div className="bg-slate-50 rounded-lg p-4">
                    <h3 className="text-lg font-semibold text-slate-800 mb-4">Throughput by Year</h3>
                    <ResponsiveContainer width="100%" height={300}>
                      <BarChart data={yearChartData}>
                        <CartesianGrid strokeDasharray="3 3" />
                        <XAxis dataKey="year" />
                        <YAxis />
                        <Tooltip />
                        <Bar dataKey="count" fill="#3b82f6" />
                      </BarChart>
                    </ResponsiveContainer>
                  </div>

                  {/* Jobs by Route */}
                  <div className="bg-slate-50 rounded-lg p-4 col-span-2">
                    <h3 className="text-lg font-semibold text-slate-800 mb-4">Jobs by Route Type</h3>
                    <ResponsiveContainer width="100%" height={300}>
                      <BarChart data={routeChartData}>
                        <CartesianGrid strokeDasharray="3 3" />
                        <XAxis dataKey="name" />
                        <YAxis />
                        <Tooltip />
                        <Legend />
                        <Bar dataKey="count" fill="#10b981" />
                      </BarChart>
                    </ResponsiveContainer>
                  </div>
                </div>

                {/* Data Table */}
                <div>
                  <h3 className="text-lg font-semibold text-slate-800 mb-4">Recent Jobs</h3>
                  <div className="border border-slate-200 rounded-lg overflow-hidden">
                    <div className="overflow-x-auto max-h-96 overflow-y-auto">
                      <table className="w-full text-sm">
                        <thead className="bg-slate-100 sticky top-0">
                          <tr>
                            <th className="text-left px-4 py-3 font-medium text-slate-700">Job Name</th>
                            <th className="text-left px-4 py-3 font-medium text-slate-700">Type</th>
                            <th className="text-left px-4 py-3 font-medium text-slate-700">Route</th>
                            <th className="text-left px-4 py-3 font-medium text-slate-700">Customer PN</th>
                            <th className="text-left px-4 py-3 font-medium text-slate-700">Rev</th>
                            <th className="text-left px-4 py-3 font-medium text-slate-700">Start Time</th>
                            <th className="text-left px-4 py-3 font-medium text-slate-700">End Time</th>
                          </tr>
                        </thead>
                        <tbody>
                          {teamBacklog.slice(0, 50).map((item) => (
                            <tr key={item.id} className="border-t border-slate-200 hover:bg-slate-50">
                              <td className="px-4 py-3 font-medium">{item.name}</td>
                              <td className="px-4 py-3">
                                <span className="px-2 py-1 bg-blue-100 text-blue-700 rounded text-xs">
                                  {item.job_type}
                                </span>
                              </td>
                              <td className="px-4 py-3">{item.routeType}</td>
                              <td className="px-4 py-3">{item.cust_pn || '-'}</td>
                              <td className="px-4 py-3">{item.rev || '-'}</td>
                              <td className="px-4 py-3">
                                {new Date(item.startTime).toLocaleDateString()}
                              </td>
                              <td className="px-4 py-3">
                                {item.endTime ? new Date(item.endTime).toLocaleDateString() : 'In Progress'}
                              </td>
                            </tr>
                          ))}
                        </tbody>
                      </table>
                    </div>
                  </div>
                  {teamBacklog.length > 50 && (
                    <p className="text-sm text-slate-600 mt-2">
                      Showing 50 of {teamBacklog.length} jobs
                    </p>
                  )}
                </div>
              </div>
            )}
          </div>
        )}
      </div>

      {/* My Backlog Accordion */}
      <div className="bg-white rounded-lg shadow">
        <button
          onClick={() => setMyBacklogOpen(!myBacklogOpen)}
          className="w-full flex items-center justify-between p-6 hover:bg-slate-50 transition-colors"
        >
          <div className="flex items-center gap-3">
            <CheckCircle className="text-green-600" size={24} />
            <div className="text-left">
              <h2 className="text-xl font-bold text-slate-800">My Backlog</h2>
              <p className="text-sm text-slate-600">Personal tasks and assignments</p>
            </div>
          </div>
          {myBacklogOpen ? <ChevronUp size={24} /> : <ChevronDown size={24} />}
        </button>

        {myBacklogOpen && (
          <div className="p-6 border-t border-slate-200">
            <div className="text-center py-12 text-slate-500">
              <CheckCircle size={48} className="mx-auto mb-4 opacity-50" />
              <p>My Backlog coming soon</p>
              <p className="text-sm mt-2">Personal task tracking will be added here</p>
            </div>
          </div>
        )}
      </div>
    </div>
  )
}